@font-face {
  font-family: "Pretendard-Bold";
  src: url("./media/fonts/Pretendard-Bold.woff") format("woff");
}


@font-face {
  font-family: "Pretendard-Light";
  src: url("./media/fonts/Pretendard-Light.woff") format("woff");
}

@font-face {
  font-family: "Pretendard-Medium";
  src: url("./media/fonts/Pretendard-Medium.woff") format("woff");
}

@font-face {
  font-family: "Pretendard-Regular";
  src: url("./media/fonts/Pretendard-Regular.woff") format("woff");
}

@font-face {
  font-family: "Pretendard-SemiBold";
  src: url("./media/fonts/Pretendard-SemiBold.woff") format("woff");
}


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 16px;
	font: inherit;
	vertical-align: baseline;
}

@media (max-width: 420px) {
  html {
    font-size: 12px;  
  }
}





html, body {
  word-break: keep-all;
  word-wrap: break-word;
}

body {
  font-family: "Pretendard-Regular", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  line-height: 1;
  display: block;
  background-color: white;

}

a{
  text-decoration: none;
}

li{
  list-style: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background-container{
  position: absolute;
  width: 100%;
  height: 100vh;
  top: -60px;
  z-index: -5000;
  overflow: hidden;
}
.background-container img{
  min-height: 100vh;
}

.global-wrapper{
  background-color: '';
  display: flex;
  max-width: calc(100% - 40px);
  width: 95%;
  margin:0 auto; 
}

.global-flex-container{
  display: flex;
  /* flex: 1; */
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;   
}

