#modalDimmerStyle{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2000;
    width: 100%;
    height: 100%;
    background-color: #00000080;
}

#modalSearchStyle{
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 50%;
    transform: translate(-50%, 0%);
    width: 100%;
    height: 100%;
    max-width: 760px;
    max-height: 850px;
    background-color: #1aaedb;
    border: 2px solid #5C903A;
    z-index: 2001;
}

#modalSearchBarStyle{
    display: flex;
    justify-content: space-between;
    height: 60px;
    width: 100%;
    background-color: #ffffff;
    border-bottom: 1px solid #5C903A;
    padding: 0px 15px 0px 15px;
    box-sizing: border-box;
}

#modalSearchInputStyle{
    all: unset;
    height: 100%;
    width: 100%;
    font-size: 16px;
    float: left;
}

#modalSearchResultsContainer{   
    background-color: rgb(255, 255, 255);
    height: 100%;
}

#modalFooter{
    background-color: #EEEEEE; 
    height: 40px;
    width: 100%; 
    padding: 11px 15px 0px 0px; 
    box-sizing: border-box; 
    border-top: 1px solid #5C903A; 
    text-align: right; 
}

.searchIndexContainer{
    max-width: calc(100% - 30px);
    width: 95%;
    margin:0 auto; 
}

.searchResultHeader{
    padding: 20px 0px 0px 0px;
    font-family: 'Pretendard-SemiBold';
    color: #385723;
}


.searchResultCardContainer{
    background-color: rgb(248, 248, 248);
    padding: 12px 15px 12px 15px;
    border: 1px solid #a7a7a7;
    border-radius: 10px;
    cursor: pointer;
    color: #505050;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;


}

.searchResultCardContainer:hover{
    border: 1px solid #385723;
    background-color: rgb(248, 248, 248);
}

.searchResultCardContainer:hover .searchResultCardContainer-title{
    color: #385723;
    text-decoration: underline;
}


.searchResultCardContainer-left{
    display: inline-block;
    align-content: center;
    max-width: 50%;
    overflow-x: visible;
}


.searchResultCardContainer-title{
    font-family: 'Pretendard-SemiBold';
    font-size: 16px;
    line-height: 21px;
}

.searchResultCardContainer-subtitle{
    font-family: 'Pretendard-Regular';
    font-size: 13px;
    color: #777777;
    display: block;
    margin-top: 4px;
    line-height: 17px;
}
.searchResultCardContainer-snippet{
    font-family: 'Pretendard-Regular';
    font-size: 13px;
    color: #777777;
    line-height: 18px;
    display: block;
    margin-top: 24px;
    width: 150%;
}
.searchResultCardContainer-right{
    max-width: 48%;
    text-align: right;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: top;
    font-size: 14px;
    background-color: '';

}

.searchResultCardContainer-producttype{
    display: flex;
    justify-self: flex-end;
    padding-bottom: 8px;
    margin: 3px 0px 0px 0px;
    
}

.searchResultCardContainer-ticker{
    margin: 3px 0px 0px 10px;
}
.searchResultCardContainer-flag {
    margin: 3px 0px 0px 10px;
    height: fit-content;

}
.searchResultCardContainer-flag > img{
    width: 23px;  
}

.searchResultCardContainer-facade{
    margin-left: 25px;
}

.searchResultCardContainer-facade > img{
    height: 50px;  
}

.searchResultCardContainer-thumbnail{
    width: 100%;
    background-color: '';
}

.searchResultCardContainer-thumbnail > img{
    width: 100%;
    max-height: 70px;      
}

