#header-background{
    width: 100%;
    background-color: transparent;
    border-bottom: 1px solid #5C903A;
    display: flex;
    justify-content: center;
    position: -webkit-sticky; /* 사파리 브라우저 지원 */
    position: sticky;
    top: 0px;
    z-index: 2000;
    transition: all 0.4s;
}
#header-background-change{
    width: 100%;
    background-color: rgba(255, 255, 255, 0.863);
    border-bottom: 1px solid #5C903A;
    display: flex;
    justify-content: center;
    position: -webkit-sticky; /* 사파리 브라우저 지원 */
    position: sticky;
    top: 0px;
    z-index: 2000;
    transition: all 0.4s;
}

#header-container-default{
    display: flex;
    width: 90%;
    background-color: transparent;
    max-width: calc(100% - 40px);
    height: 60px;
}


#header-container-logo{
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding-top: 4px;
    margin-right: 10px;
}

#header-container-nav{
    display: flex;
    flex: 1;
    overflow-y: hidden;
    max-height: 100%;
    justify-content: space-between;
}

.nav-menu{
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;
    list-style: none;
    overflow: hidden;
}

.nav-menu-searchbutton{
    display: flex;
    width: auto;
    align-items: center;
    padding: 12px 0px 12px 0px ;
    background-color: "";
}

.nav-menu-searchbutton-disable{
    display: none;    
}


/* ------------------not selected-------------------------------- */
.nav-menu-item{
    font-family: "Pretendard-SemiBold";
    font-size: 17px;
    position: relative;
    height: 100%;
    /* display: flex; */
    /* align-items: center; */
    padding: 0 21px;
    color: #888888;
}

.nav-menu-item:hover{
    color : #5C903A;
}

.nav-menu-item::before{
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    bottom: -20px;
    left: 0;
    background-color: #5C903A;
    transform: scaleX(0);
    transition: transform 0.5s ease;
}

.nav-menu-item:hover::before {
    transform: scaleX(1);
}

/* -----------------------선택--------------------------- */

.nav-menu-item:active, .nav-menu-item.active{  
    color :#5C903A;    
}  

.nav-menu-mobile{
    position: fixed;
    z-index: 9000;
    width: 100%;
    height: 100%;
    background-color: '';
    display: flex;
    justify-content: right;
}

.nav-menu-item-mobile {
    font-family: 'Pretendard-SemiBold';
    color: #385723;
}

