.productchip-container{
    background-color: rgba(255, 255, 255, 0.863);
    padding: 10px;
    border: 1px solid #385723;
    border-radius: 5px;
    /* max-width: 274px; */
    max-height: 70px;
    overflow: hidden;
    display: flex;
    flex: 1 1 274px;
    align-items: center;
    cursor: pointer;
    -webkit-box-shadow: 10px 10px 11px -5px rgba(214,214,214,1);
    -moz-box-shadow: 10px 10px 11px -5px rgba(214,214,214,1);
    box-shadow: 10px 10px 11px -5px rgba(214,214,214,1);
}
#productchip-container-margin{
    margin: 5px;
}
.productchip-container:hover{
    background-color: #F6F6F6;
}

.productchip-container-info{
    background-color: "";
    width: 70%;
    padding-right: 2px;
}
.productchip-container-info-name{
    font-family: 'Pretendard-SemiBold';
    font-size: 15px;
    color: #385723
}
.productchip-container-info-desc{
    font-family: 'Pretendard-Medium';
    font-size: 13px;
    color: #8E8E8E;
    padding-top: 2px;
    box-sizing: border-box;
    line-height: 15px;
}

.productchip-container-logo{
    display: flex;
    justify-content: flex-end;
    background-color: '';
    width: 30%;
    background-color: '';
}
.productchip-container-logo img{
    max-width: min(125px, 100%); 
    max-height: 35px;
}